      link: "https://www.meetup.com/Azure-DEV-Munich/events/266531235/",
      description:
        '<p>+++ This is a shared Meetup with München .Net Meetup +++<br/>+++ Origin Event: <a href="https://www.meetup.com/de-DE/Munchen-NET-Meetup/events/266381022/" class="linkified">https://www.meetup.com/de-DE/Munchen-NET-Meetup/events/266381022/</a> +++</p> <p>Microsoft AI MVP Georgia Kalyva, will talk about how to use the Azure Bot Service together with Azure Cognitive Services, to create a virtual help-desk assistant in 3 simple steps: First, create a knowledge base, then create a natural language model using LUIS.ai (Language Understanding Intelligent Service) and finally combine everything to create and deploy the bot to the Azure App Service, to be publicly available everywhere. Publish options include Skype, Teams and more.</p> <p>AGENDA<br/>---------------------------------------------------------------<br/>• 17:30 Doors Open<br/>• 18:00 Welcome note - Connect with Georgia<br/>• 18:05 Introduction to Azure Bot Service<br/>• 18:20 How to create a knowledge base using the QnA Maker<br/>• 18:35 Introduction to LUIS.ai<br/>• 19:00 Create and testing our bot<br/>• 19:10 Other publish options<br/>• 19:20 Q&amp;Α<br/>• 20:00 End of Event - Glühwein oder Bier?</p> <p>DISCLAIMER<br/>---------------------------------------------------------------<br/>We will connect with Georgia from Skype and the event will be presented remotely. Since Georgia will not actually be present, cameras will be on!</p> ',
