        '<p>Join the Geomob Munich Team!</p> <p>Did you enjoy the Geomob Munich meetups so far? Whether it was for fun or business, we certainly hope you had a good time at the 3 events so far.</p> <p>Geomob Munich is similar to an open source project. We want to expand the team and get more people involved in organizing the events for the coming year.</p> <p>We are constantly looking at ways to improve the event and make it more fun and useful. This is your chance to sit down with us and help build a strong spatial community here in Munich.</p> <p>The plan is to meet at 16.45 close to Wayra where the meetup on "Mobility and Location Intelligence" is scheduled the same evening as our team meeting. Lets get a coffee and have a chat about Geomob Munich 2020. After that we head to Wayra for the 4th Geomob Munich meetup :)</p> <p>If you have any questions, please feel free to reach out to us via meetup or via email at [masked]</p> <p>Your Geomob Munich Team,<br/>Muthu, Abhi, Florian (and hopefully you!)</p> ',
