      description:
        '<p>HAMBURG’S BEST STARTUPS VISIT MUNICH</p> <p>we connect startups with matching investors.</p> <p>Wir laden alle Startups, Investoren und sonstige Interessierte herzlich ein, an diesem Abend dabei zu sein!</p> <p>Die fünf Gewinner unserer Hamburger Startup Competition pitchen ihr Business vor interessierten Investoren und Publikum in München. Wir freuen uns auf einen tollen Abend in entspannter Atmosphäre!</p> <p>Ablauf:</p> <p>Beginn um 19:00 Uhr<br/>Pitch eines erfolgreichen, lokalen Startups<br/>5 Pitches der besten Hamburger Startups<br/>Q&amp;A mit Münchner Investoren und Startup Community<br/>Lokale Jury wählt das Gewinnerstartup des Abends<br/>Networking an der Bar<br/>Ende um 22:00 Uhr</p> <p>Alle Infos zu uns und unseren Events findest Du hier: <a href="https://www.meetup.com/de-DE/wow-yes-Startups-Munchen/" class="linkified">https://www.meetup.com/de-DE/wow-yes-Startups-Munchen/</a></p> <p>******************************************</p> <p>ENGLISH</p> <p>You are a startup, investor or just curious about the startup community? Then you are cordially invited to meet Hamburg’s best startups in Munich!</p> <p>The five winners of our Hamburg Startup Competition will pitch their business in front of interested investors and the public in Munich. We are looking forward to a great evening in a relaxed atmosphere!</p> <p>Agenda:</p> <p>Start at 7 p.m.<br/>Pitch of a successful, local startup<br/>5 pitches from Hamburg’s best startups<br/>Q&amp;A with Munich investors and startup community<br/>Local jury selects the winner startup of the evening<br/>Networking at the bar<br/>End at 10 p.m.</p> <p>You can find all information about us and our events here: <a href="https://www.meetup.com/de-DE/wow-yes-Startups-Munchen/" class="linkified">https://www.meetup.com/de-DE/wow-yes-Startups-Munchen/</a></p> ',
