        "https://www.meetup.com/Applied-Category-Theory-Munich/events/264426961/",
      description:
        '<p>3rd Meetup of the Applied Category Theory Munich group.<br/>Chapter 3: "Databases: Categories, Functors, and Universal Constructions"</p> <p>-----------------------------------------------------<br/>TL;DR<br/>- start &lt;[masked] Thu 18:30&gt; @ Tropeninstitut LMU, Leopoldstr. 7, 3rd floor, Room 305<br/>- review Chapter 3<br/>-----------------------------------------------------</p> <p>Let\'s continue and read Chapter 3 of "Seven Sketches in Compositionality"<br/>until &lt;[masked] Thu&gt;, and we meet again 6:30 pm at the "Department of Infectious Diseases &amp; Tropical Medicine".</p> <p>There will be no food, so please eat before or bring your own dinner.</p> <p>In the same format as last time, we go through the main definitions, theorems, examples and exercises in the chapter. Please feel invited to also come up with interesting questions, applications, maybe examples from your own work.</p> <p>Video lectures covering the Chapter 3 can be found here:<br/><a href="https://ocw.mit.edu/courses/mathematics/18-s097-applied-category-theory-january-iap-2019/lecture-videos-and-readings/chapter-3-databases-categories-functors-and-co-limits/" class="linkified">https://ocw.mit.edu/courses/mathematics/18-s097-applied-category-theory-january-iap-2019/lecture-videos-and-readings/chapter-3-databases-categories-functors-and-co-limits/</a></p> <p>We also started a git repository to share interesting links:<br/><a href="https://github.com/drever/act-munich" class="linkified">https://github.com/drever/act-munich</a></p> <p>If you have wishes, ideas, suggestions to enrich the meetup, please reach out to us.</p> <p>If you cannot make it, please update your RSVP status as early as possible to allow members on the waiting list to participate.</p> ',
