      description:
        '<p>Liebe Mitglieder der Meetup-Gruppe Software Architektur München,</p> <p>unser nächstes Treffen steht an und wir haben zwei spannende Vorträge:</p> <p>Vortrag: "Rettet den Mud-olith!<br/>Sprecher: Daniel Götz und Peter Gafert</p> <p>Vortrag: Qualitäts-Türchen statt Quality-Gates<br/>Sprecher: Florian Deißenböck</p> <p>Abstracts finden sich wie immer auf unsere Seite <a href="https://swa-muc.de/" class="linkified">https://swa-muc.de/</a></p> <p>Wir werden uns am 10. Dezember 2019 um 18:30 Uhr in den Räumen von TNG Technology Consulting GmbH in der Arabellastraße treffen:</p> <p>Arabellastraße 4a<br/>81925 München</p> <p>ca. 3 Minuten von der U-Bahn-Haltestelle der U4 / Arabellapark weg, mit dem Auto kann man hier parken: <a href="https://en.parkopedia.com/parking/locations/arabellastraße_4a_81925_münchen_deutschland_u283bq4bkek/?arriving=[masked]&amp;leaving=[masked]" class="linkified">https://en.parkopedia.com/parking/locations/arabellastraße_4a_81925_münchen_deutschland_u283bq4bkek/?arriving=[masked]&amp;leaving=[masked]</a></p> <p>Für Getränke und Essen ist gesorgt.</p> ',
