      link:
        "https://www.meetup.com/Materna-Digital-Hub-Muenchen/events/266314095/",
      description:
        '<p>Wir laden Sie herzlich ein zu unserem After-Work-Event „Materna Digital hub“ in München am 26. November 2019 ab 17.00 Uhr!</p> <p>RPA wird von immer mehr Organisationen verwendet, um die Produktivität an PC-Arbeitsplätzen durch Automatisierung weiter zu verbessen.</p> <p>Seine wahren Vorteile spielt RPA erst aus, wenn Sie kognitive Intelligenz, maschinelles Lernen und robotergesteuerte Prozessautomatisierung kombinieren. Damit befreien Sie Mitarbeiter von wiederkehrenden Aufgaben, steigern die Effizienz an den Arbeitsplätzen und senken gleichzeitig die Fehlerrate bei manuellen Dateneingaben.</p> <p>Damit Sie sich ein konkretes Bild von der Leistungsfähigkeit einer solchen Lösung machen können, zeigen wir Ihnen eine Live-Demo auf Basis der Software Automation Anywhere.</p> <p>Das erwartet Sie am 26. September von 17:00 bis 20:00 Uhr<br/> Robotic Process Automation vs. Makro-Rekorder: Was ist RPA und warum ist das jetzt (wieder) ein Hype-Thema? (Materna)<br/> Robotic Process Automation oder API: Für jeden Anwendungsfall die richtige Automatisierung (Materna)<br/> Anwendungsfälle für Robotic Process Automation sowie Demo (Materna)<br/> Next-level Robotic Process Automation: Wie bringt Automation Anywhere "cognitive" in die Automatisierung? Vorstellung kognitiver RPA-Anwendungsfälle und IQBot Demo (Automation Anywhere)</p> <p>Im Anschluss freuen wir uns auf den Austausch bei Canapés und Co.</p> <p>Die Teilnahme ist kostenlos für Sie.</p> ',
