      description:
        '<p>Karriere-Event jobs and master</p> <p>Finde den Job, der zu deinen Stärken passt. Das Karriere-Event jobs and master stellt dir mehr als 1.000 attraktive Jobangebote und Masterprogramme vor. Ein Matching System führt dich direkt zu den richtigen Unternehmen und Hochschulen. Spannende Games geben dir die Möglichkeit, deine Fähigkeiten deinem künftigen Arbeitgeber zu präsentieren. Geboten sind außerdem interessante Infos zu Trends und Entwicklungen im Arbeitsmarkt 4.0, den digitalen Berufen und die Chance, in kleinem Kreis zu diskutieren, Leute kennenzulernen und sich neu zu vernetzen.</p> <p>WER? Studierende, Absolventen und Young Professionals der Fachrichtungen MINT, Wirtschafts- und Geisteswissenschaften</p> <p>WANN? Donnerstag, 9. Januar 2020, 14-19 Uhr</p> <p>WO? Kulturhalle Zenith, Lilienthalallee 29, 80939 München</p> <p>WAS? Jobmesse, Masterstudiengänge, interaktiver Career Parcours, Workshops, Coachings, Fachvorträge und Get-Together</p> <p>Infos und Gratis-Ticket sichern unter <a href="https://www.jobsandmaster.de/" class="linkified">https://www.jobsandmaster.de/</a></p> ',
