        "https://www.meetup.com/AI-for-Business-in-Munchen-Use-cases-and-Networking/events/265735738/",
      description:
        '<p>Unser erstes AI for Business Meetup findet am 23. Januar 2020 in München statt.</p> <p>Wir haben bereits viele erfolgreiche Meetups in Zürich, Bern und Berlin gehabt und freuen uns nun riesig auf eine (neue) Community in München.</p> <p>Unsere Agenda ist gefüllt mit 2 super Speakern und einem tollen Apero, (von LinkedIn Learning organisiert).</p> <p>18:00 - Einlass &amp; Registrierung</p> <p>18:30 - Keynote - tbd.</p> <p>18:45 - Stephan Bail - Talk tbd</p> <p>19:15 - Marco Hochstrasser - AI für Predictive Analytics</p> <p>19:45 - Snacks, Drinks, Networking</p> <p>21:00 - Veranstaltungsende</p> <p>Wenn ihr unsere Speaker bereits vorher "kennen lernen" wollt, dann schaut auf LinkedIn vorbei:<br/>Stephan Bail: <a href="https://www.linkedin.com/in/stephan-bail/" class="linkified">https://www.linkedin.com/in/stephan-bail/</a><br/>Marco Hochstrasser: <a href="https://www.linkedin.com/in/mhochstr/" class="linkified">https://www.linkedin.com/in/mhochstr/</a></p> <p>Und wir sind natürlich auch bereits auf LinkedIn vertreten. Hier gibts regelmässig News und Updates rund um den Einsatz von Künstliche Intelligenz im Business.<br/><a href="https://www.linkedin.com/company/ai-zurich/" class="linkified">https://www.linkedin.com/company/ai-zurich/</a></p> <p>Bis bald<br/>Eure Sophie Hundertmark</p> <p>P.S.: Verpasst nicht unsere Grosse AI FOR BUSINESS KONFERENZ am[masked] in Zürich.<br/><a href="https://ai-zurich.ch/" class="linkified">https://ai-zurich.ch/</a><br/>(Für alle Nicht-CH Gäste gibt es die Zürich Card für 3 Tage dazu geschenkt.)</p> ',
