      link:
        "https://www.meetup.com/Shopware-Developers-Muenchen/events/265920923/",
      description:
        '<p>Du bist neugierig auf Shopware 6, weißt aber nicht, wo Du anfangen sollst? Du bist (noch) kein Teil des Shopware-Ökosystems? Dann solltest Du unser Developer Event “Meet Shopware 6” nicht verpassen. Wir zeigen Dir, was uns an Shopware begeistert, warum wir es lieben, mit Shopware zu arbeiten und warum auch Du es lieben wirst. Wenn Du mit Symfony, Bootstrap und Vue.js vertraut bist, wirst Du Dich wie zu Hause fühlen. Auch wenn nicht - die Entwicklung mit Shopware 6 fühlt sich extrem intuitiv an*. Während des Events werden wir es beweisen: Du wirst die Shopware-Entwickler treffen und wir werden gemeinsam erste Erweiterungen bauen.</p> <p>Beginne Deine Reise mit Shopware 6 noch heute - Registriere Dich hier: <a href="https://www.shopware.com/en/devs-meet-shopware-6-munich/" class="linkified">https://www.shopware.com/en/devs-meet-shopware-6-munich/</a></p> <p>Bitte beachte: Das Dev Event wird auf Englisch sein, die Shopware Entwickler sprechen natürlich auch Deutsch.</p> <p>Voraussetzungen:<br/>- Laptop<br/>- Running Shopware developer setup<br/>- Basic knowledge on Symfony &amp; Vue.js<br/>- Curiosity &amp; Team spirit</p> <p>Agenda:<br/>09:00 - 09:30 am: Opening and a small breakfast<br/>09:30 - 10:30 am: Welcome and Kickoff Shopware 6<br/>10:30 - 12:00 am: Onboarding Development Shopware 6<br/>12:00 - 12:45 am: Lunch<br/>12:45 - 04:45 pm: Development Session Shopware 6<br/>04:45 - 05:15 pm: Recap / Q&amp;A Session<br/>05:15 - 06:00 pm: Networking</p> <p>Jetzt kostenlos registrieren: <a href="https://www.shopware.com/en/devs-meet-shopware-6-munich/" class="linkified">https://www.shopware.com/en/devs-meet-shopware-6-munich/</a></p> ',
