      link:
        "https://www.meetup.com/mediatrends_and_innovation/events/266578032/",
      description:
        '<p>Innovators Connect - Boost your 2020 Innovation Strategy!</p> <p>2020 is das Jahr in dem wir Innovationsprojekte zur Realität werden lassen. Mit welchen Strategien und welchen Tools? Wir zeigen es euch bei unserem Hands-On Demo Day!</p> <p>An welchen Trends kommt euer Unternehmen 2020 nicht vorbei? Mit welchen Strategien und Methoden werden Innovationsprojekte in Innovation Labs anderer Branchen erfolgreich umgesetzt? Und wie klappt die Kooperation mit Startups langfristig?</p> <p>In interaktiven Fish-Bowl Diskussionen mit Experten und hands-on Workshops mit unseren Startups könnt ihr eure Strategie für erfolgreiche Innovationsprojekte 2020 optimieren!</p> <p>Bei Innovators Connect erwarten euch ...</p> <p>- Visionäre Speaker mit Trend-Outlook (u.a. 5G, Future of Digital Advertising, AI und Datenjournalismus)<br/>- Innovation Labs außerhalb der Medienindustrie<br/>- Startup Kooperationen eintüten: Workshops mit unseren Startups aus Batch#6<br/>- Startup Mindset &amp; Learnings: Final Pitches vom Batch#5 - mit allen Learnings der letzten 9 Monate<br/>- Durchstarter Tipps 2020: Release des Media Lab Innovation Guidebooks<br/>- ganz viele Plätzchen, Punsch &amp; Santa Vibes</p> <p>- AGENDA SNEAK PEAK -</p> <p>13:30 | Doors open<br/>14:00 | Welcome &amp; Innovation - 2020! So starten wir nächstes Jahr durch<br/>14:15 | Keynote - Rethink Digital Advertising<br/>14:45 | Fishbowl-Discussion: Innovation Labs außerhalb der Medien<br/>15:30 | Shortpitches by Media Startup Fellowship Batch #6<br/>15:40 | Coffee Break<br/>16:00 | Workshops by Media Startup Fellowship Batch#6<br/>&gt; Community-Building mit 1E9<br/>&gt; KPIs for digital PR mit aclipp<br/>&gt; EdTech meets Media mit flowra<br/>&gt; Data-driven Publishing mit Focal Analytics<br/>&gt; FitTech in Media mit Jalu<br/>&gt; Reader Engagement mit RosaMag<br/>&gt; Globalising Journalism mit The Story Market<br/>&gt; Re-inventing the Mobile Ad Market mit Veeplay<br/>16:40 | Learnings by Media Startup Fellowship Batch#5<br/>&gt; flint.gg: Den Gaming Markt verstehen lernen<br/>&gt; Kerngedanke: Social Kanäle richtig bespielen<br/>&gt; Luminovo: Dokumente und Texte automatisiert bearbeiten<br/>&gt; Sidepreneur: Wie man innovative Mitarbeiter fördert<br/>&gt; Varia: neue Perspektiven mit KI-Filtern<br/>&gt; Webmag: Corporate Publishing neu erfinden<br/>17:15 | Closing Keynote: Leap into 2020 - An diesen Trends kommt ihr nicht vorbei<br/>AB 18:30 Media Lab Innovation Guide Release Party<br/>&amp; Startup-Xmas Party</p> <p>...mehr Info kommt bald</p> <p>Registriere dich gerne schon vorab für das Event:<br/><a href="https://www.media-lab.de/innovators-connect#Anmelden" class="linkified">https://www.media-lab.de/innovators-connect#Anmelden</a></p> ',
