        "<p>Traffic in German cities is increasing, and so are the challenges.<br/>To ensure that urban mobility remains efficient and safe in the<br/>future, alternative mobility concepts can be offered through<br/>new technological developments and urban mobility can be made<br/>city-compatible. Almost all new usage models are available on<br/>innovative platforms. In our MeetUp we would like to have a 360-<br/>degree view on urban mobility platforms with 3 experts from 3<br/>different perspectives - the business, operations and technology<br/>perspective.<br/>Afterwards there will be a discussion with our visitors and the<br/>opportunity for networking.</p> <p>AGENDA<br/>6:30 pm Doors open</p> <p>7:00 pm Welcome note from hosts</p> <p>7:15 pm Business View on urban mobility platforms<br/>(Dr. Mario Lochmüller, CEO &amp; Founder of Moovster)</p> <p>7:40 pm Operational View on urban mobility platforms<br/>(Philipp Risse, Regional Operations Manager South Germany, TIER Mobility)</p> <p>8:05 pm Technology View on urban mobility platforms<br/>(Steffen Krause, IT Architect &amp; Smarter Mobility Lab<br/>Leader IBM)</p> <p>8:30 pm Panel Discussion with all keynote speakers and open<br/>questions from audience</p> <p>9:00 pm Beer, Pizza &amp; Networking</p> <p>****************Please note this Meetup is hold in German******************</p> ",
